@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Lato', sans-serif}body{background:#111}section{position:relative;width:100%;height:100vh;min-height:680px;overflow:hidden;display:flex;justify-content:center;align-items:center}section h2{color:#fff;font-size:12em;-ms-user-select:none;-moz-user-select:none;-khtml-user-select:none;-webkit-user-select:none}section .wrap{position:absolute;width:620px;min-height:420px;background-color:rgba(75,0,130,0.5);border-radius:6px;z-index:10;display:flex;flex-direction:column;justify-content:space-between}section .info{text-align:justify;line-height:2.0rem;letter-spacing:1px;font-size:1.2rem;padding:25px 35px;color:#fff;-ms-user-select:none;-moz-user-select:none;-khtml-user-select:none;-webkit-user-select:none}section .payment{display:flex;flex-direction:column;align-items:center}@media (min-width: 320px) and (max-width: 480px){section h2{display:none;font-size:4.2rem}}@media (min-width: 481px) and (max-width: 767px){section h2{display:none;font-size:5.2rem}}@media (min-width: 768px) and (max-width: 1200px){section h2{display:none;font-size:10.2rem}}

section img{position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover;-ms-user-select:none;-moz-user-select:none;-khtml-user-select:none;-webkit-user-select:none}

section .fail{border-radius:8px;background-color:red;height:70px;width:450px}

section .success{border-radius:8px;background-color:greenyellow;height:70px;width:450px}

section .result{border-radius:8px;background-color:salmon;height:70px;width:450px;color:aliceblue}

section .contact{padding:15px 0px;color:#fff}section .contact a{font-size:1.3rem;text-decoration:none;color:greenyellow}

section .nopage{border-radius:8px;background-color:yellow;height:70px;width:450px;color:black}

